import React, { useState } from "react";
import "./Onglets.css";
import "./Slide.css";

import SlideshowColliers from "./SlideColliers";
import SlideshowMariage from "./SlideMariage";
import SlideshowColliersLyto from "./SlideColliersLyto";

import productsColliers from "./products/ProductsColliers";
import productsMariage from "./products/ProductsMariage";
import productsColliersLyto from "./products/ProductsColliersLyto";

export default function Onglets() {
  const [stateOnglets, setStateOnglets] = useState(1);

  const go1 = () => {
    setStateOnglets(1);
  };

  const go2 = () => {
    setStateOnglets(2);
  };

  const go3 = () => {
    setStateOnglets(3);
  };

  

  return (
    <div className="contain">
      <div className="contBtn">
        <div
          onClick={go1}
          className={`onglets ${stateOnglets === 1 ? "active" : ""}`}
        >
          {" "}
          <h1>Colliers en fil d'aluminium.<img src="https://php.milhie.fr/images/colliers/collier1_259.jpeg"/></h1>
        </div>
        <div
          onClick={go2}
          className={`onglets ${stateOnglets === 2 ? "active" : ""}`}
        >
          <h1>Colliers spécial mariage.<img src="https://php.milhie.fr/images/under.jpg" /></h1>
        </div>
        <div
          onClick={go3}
          className={`onglets ${stateOnglets === 3 ? "active" : ""}`}
        >
          <h1>Colliers lytothérapie.<img src="https://php.milhie.fr/images/under.jpg" /></h1>
        </div>
        
      </div>
      <div className="container">
        {stateOnglets === 1 ? (
          <div className="cont">
            <section className="slideshowColliers">
              {productsColliers.map(product => (
                <SlideshowColliers
                  title={product.title}
                  price={product.price}
                  description={product.description}
                  link={product.link}
                  reference={product.reference}
                  images={product.images}
                />
              ))}
            </section>
          </div>
        ) : stateOnglets === 2 ? (
          <div className="cont">
            <section className="slideshowMariage">
              {productsMariage.map(product => (
                <SlideshowMariage
                  title={product.title}
                  price={product.price}
                  description={product.description}
                  link={product.link}
                  reference={product.reference}
                  images={product.images}
                />
              ))}
            </section>
          </div>
        ) :  (
          <div className="cont">
            <section className="slideshowColliersLyto">
              {productsColliersLyto.map(product => (
                <SlideshowColliersLyto
                  title={product.title}
                  price={product.price}
                  description={product.description}
                  link={product.link}
                  reference={product.reference}
                  images={product.images}
                />
              ))}
            </section>
          </div>
        
        )
        
      }
      </div>
    </div>
  );
}
